<div class="faqs">
    {#if leaveMessage === true}
        <div class="closeit">
            <svg on:click={() => {leaveMessage=false}} style="width: 15px" aria-hidden="true" focusable="false"
                 data-icon="times" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 352 512">
                <path fill="#333"
                      d="M242.72 256l100.07-100.07c12.28-12.28 12.28-32.19 0-44.48l-22.24-22.24c-12.28-12.28-32.19-12.28-44.48 0L176 189.28 75.93 89.21c-12.28-12.28-32.19-12.28-44.48 0L9.21 111.45c-12.28 12.28-12.28 32.19 0 44.48L109.28 256 9.21 356.07c-12.28 12.28-12.28 32.19 0 44.48l22.24 22.24c12.28 12.28 32.2 12.28 44.48 0L176 322.72l100.07 100.07c12.28 12.28 32.2 12.28 44.48 0l22.24-22.24c12.28-12.28 12.28-32.19 0-44.48L242.72 256z"></path>
            </svg>
        </div>
        <LeaveMessage {data} person="{activePerson}" bgcolor="{bgcolor}"/>
    {:else}
        <div class="faqs-title">{faqs.title}</div>
        <div class="faqs-shortcuts">
            {#each faqs.faqs as faq, i}
                {#if faq.shortcutName !== null }

                    <span  on:click={() => {
                        showAll = false;
                        if (faqId === faq.id) {
                          faqId = null;
                          hidden = true;
                        }
                        else {
                          faqId = faq.id;
                          hidden = false;
                        }
                    }} class="faq-shortcut">                            
                        {#if faq.translateShortCut}{faq.translateShortCut}{:else}{faq.shortcutName}{/if}
                        <img class="sml-ico" alt="{faq.shortcutName}" src="{getproperty(faq.shortcutName,'icon')}">
                        </span>
                {/if}
            {/each}
        </div>
        <div class="faqs-content">

            {#each faqs.faqs as faq, i}
            <div class={bottomBorder(i)}>
                {#if faq.shortcutName }
                    <NewSingleFaq activeFaq="{(faqId === faq.id && hidden === false) || showAll}" bgcolor="{bgcolor}" faq="{faq}"  allowToggle="{false}"/>
                {:else}
                    <NewSingleFaq activeFaq="{true}" bgcolor="{bgcolor}" faq="{faq}" allowToggle="{true}"/>
                {/if}
            </div>
            {/each}

        </div>

        <div class="new-buttons">
            <p on:click={() => {dispatch('problem-solved')}}>
               {#if faqs.solvedButton}
                   {faqs.solvedButton}
               {:else}
                    More help?
               {/if}
            </p>
            {#if activePerson.online === true}
            <div on:click={() => {dispatch('problem-not-solved')}} style="background:{bgcolor};color:white;display:flex;align-items: center;flex:1"
                class="button">
              <img
                class="chatwith-img"
                alt="tochat-whatsapp"
                src={"https://widget.tochat.be/icon-1.png"}
              />
              <span>
                    Whatsapp
              </span>
            </div>
            {:else}
            <div on:click={() => {leaveMessage=true; dispatch('leave-message')}}
                style="background:{bgcolor};color:white" class="button">
                {#if (data.leaveMessage)}
                    {data.leaveMessage}
                {:else}
                    Leave a message
                {/if}
            </div>
            {/if}
        </div>
    {/if}
</div>

<script>
  import NewSingleFaq from './NewSingleFaq.svelte';
  import LeaveMessage from './LeaveMessage.svelte';
  import { createEventDispatcher } from 'svelte';

  const dispatch = createEventDispatcher();

  export let faqs;
  export let activePerson;
  export let bgcolor;
  export let data;

  let faqId = null;
  let hidden = true;
  let showAll = false;
  let leaveMessage = false;

  const conf = [
      {
          id: "Return",
          name: "Returns",
          color: "#F2DFC4",
          icon: "https://widget.tochat.be/img/faq-icons/box.png"
      },
      {
          id: "Broken",
          name: "Broken",
          color: "#E8B5B0",
          icon: "https://widget.tochat.be/img/faq-icons/broken-heart.png"
      },
      {
          id: "Discount",
          name: "Returns",
          color: "#D9DBC3",
          icon: "https://widget.tochat.be/img/faq-icons/money-bag.png"
      },
      {
          id: "Delivery",
          name: "Returns",
          color: "#D7EEF6",
          icon: "https://widget.tochat.be/img/faq-icons/broken-heart.png"
      },
  ]

  const  getConf = (id) => conf.find(o => o.id == id)

  const getproperty = (id,type) => {
      const obj = getConf(id)
      if(obj && obj.hasOwnProperty(type)){
          return obj[type]
      }
      else{
          return ""
      }
  }

  function bottomBorder(i){
      return i < faqs.faqs.length - 1?"bottom-border":"";
  }

</script>

<style>
    .faqs {
        max-height: 300px;
        overflow-y: auto;
        padding: 10px;
        background: white;
        color: #5c5b5b;
    } 
    .faqs-content {
        padding: 5px 5px;
        border: #efefef 1px solid;
        border-radius: 5px;
        cursor:pointer;
    }
    .bottom-border{
        border-bottom: #efefef 1px solid;
    }
    .button {
        /* width: 100%; */
        text-align: center;
        padding: 10px;
        box-sizing: border-box;
        border-radius: 5px;
        margin-bottom: 10px;
    }
    .closeit {
        text-align: right;
    }

    .faqs-title {
        color: #444847;
        font-size: 12px;
        font-weight: bold;
        margin-bottom: 10px;
        padding: 8px;
        box-shadow: 0 8px 16px 0 rgb(0 0 0 / 20%);
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
        border-top-right-radius: 10px;
    }      
    .faqs-shortcuts {
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;
        justify-content: space-evenly;
    }
    .faq-shortcut:hover {
        box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
    }
    .faq-shortcut {
        border-radius: 10px;
        margin: 2.5px;
        display: block;
        padding: 8px 10px 8px 10px;
        text-decoration: none;
        font-size: 12px;
        text-transform: capitalize;
        text-align: center;
        color: #444847;
        cursor: pointer;        
        box-shadow: 0 2px 8px 0 rgba(0,0,0,0.2);
        transition: 0.3s;
    }
    .faq-shortcut-default {
        border: 2px solid #2b2d42;
        box-sizing: border-box;
    }
    .sml-ico{
        width: auto;
        height: 15px;
        vertical-align: text-bottom;
    }
    .chatwith-img {
    height: 15px;
    width: 15px;
    display: block;
    margin-right: 5px;
    margin-left: 5px;
  }
  .new-buttons{
    margin-top:10px;
    padding-top:10px;
    display:flex;
    justify-content: center;
    cursor:pointer;
  }
  .new-buttons p{
    font-weight: bold;
    font-size: 14px;
    color: #263D62;
    flex:1
  }
</style>
